* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.--container {
  padding: 100px 150px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.counter--wrapper {
  width: 100%;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #262626;
}

.--counter {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.counter--count {
  width: 200px;
  aspect-ratio: 1;
  font-size: 2.1rem;
  background-color: #FFFFFF;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.counter--minus,
.counter--add {
  width: 70px;
  aspect-ratio: 1;
  font-size: 2rem;
  color: #FFFFFF;
  border-radius: 50%;
  border: 3px solid #FFFFFF;
  background-color: #737373;
  position: absolute;
  cursor: pointer;
}

.counter--minus {
  left: 0;
  bottom: 0;
}

.counter--add {
  right: 0;
  bottom: 0;
}

.thing--wrapper {
  width: 100%;
  min-height: 100vh;
  color: #F5F5f5;
  background-color: #737373;

}

.thing--btn {
  padding: 20px 150px;
  border: 2px solid #262626;
  border-radius: 50px;
  cursor: pointer;
  font-size: 1.2rem;
}

.object--wrapper {
  width: 100%;
  min-height: 100vh;
  background-color: #F5F5F5;
  display: flex;
  justify-content: center;
  align-items: center;

}

.--title {
  margin-block-end: 30px;
}

.object--wrapper .--card {
  width: 200px;
  border: 1px solid lightgray;
  border-radius: 10px;
  height: 350px;
  box-shadow: 5px 5px 5px 1px rgba(120, 120, 120, 0.44);
  -webkit-box-shadow: 5px 5px 5px 1px rgba(120, 120, 120, 0.44);
  -moz-box-shadow: 5px 5px 5px 1px rgba(120, 120, 120, 0.44);
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}



.object--wrapper .--card .user--img {
  width: 150px;
  align-self: center;
}

.object--wrapper .star--img {
  width: 20px;
  aspect-ratio: 1;
}

.card--name {
  margin-block: 13px;
  color: #333333;
}

.card--info {
  padding: 10px;
}

.card--favorite {
  width: 25px;
  cursor: pointer;
}

.card--contact {
  font-size: 0.75rem;
  color: gray;
  margin-block: 7px;
}